.bgBox {
  position: absolute;
  height: 7.733333rem;
  z-index: -1;
  background: #000009;
}
.index_content_locals {
  height: 8.426667rem;
  background: url(/static/img/index_pic.png) no-repeat right 0.533333rem;
}
.index_content_locals_title {
  padding-top: 0.933333rem;
}
.index_content_locals_text {
  width: 5.6rem;
  margin-top: 0.56rem;
}
.index_content_area {
  height: 8.08rem;
  background: #f7f7f7;
  overflow: hidden;
}
.index_content_area .index_content_area_img {
  width: 11.68rem;
  height: 6.4rem;
  overflow: hidden;
}
.index_content_area .index_content_area_action {
  width: 4rem;
  margin-top: 2.626667rem;
  top: 0;
  right: 0;
  position: absolute;
}
.index_content_area .index_content_area_action .common_content_title {
  margin-bottom: 0.666667rem;
}
.index_content_area .index_content_area_action .index_content_area_btn {
  margin-bottom: 0.666667rem;
}
.index_content_area .common_left_btn,
.index_content_area .common_right_btn {
  margin-top: 0.933333rem;
}
.index_content_area .common_left_btn {
  float: none!important;
  display: inline-block;
}
.index_content_area .common_left_btn:focus {
  outline: -webkit-focus-ring-color auto 0;
}
.index_content_area .common_right_btn {
  float: none!important;
  display: inline-block;
}
.index_content_area .common_right_btn:focus {
  outline: -webkit-focus-ring-color auto 0;
}
.city-swiper .swiper-wrapper {
  margin-top: 2.173333rem;
}
.city-swiper .swiper-wrapper .swiper-slide {
  width: 2.92rem;
  height: 3.933333rem;
  -webkit-transform-origin: center center 0;
  -moz-transform-origin: center center 0;
  -ms-transform-origin: center center 0;
  transform-origin: center center 0;
  -webkit-transition: all 0.1s ease 0s;
  -moz-transition: all 0.1s ease 0s;
  transition: all 0.1s ease 0s;
}
.city-swiper .swiper-wrapper .active {
  z-index: 10;
  box-shadow: 0 0 0.013333rem rgba(0, 0, 0, 0.6);
  transform: scale(1.14);
}
