  top: -4;
  text-align: left;
  background: #e84358;
}
/*实现元素宽度的过渡动画效果*/
@keyframes move {
  0% {
    width: 0;
  }
  100% {
    width: 100%;
  }
}
.common_left_btn {
  width: 0.653333rem;
  height: 0.653333rem;
  display: block;
  float: left;
  position: relative;
  cursor: pointer;
  border: 2px solid #bfbfbf;
  border-radius: 50%;
  background: url(/static/img/sprite/left.png) no-repeat center center;
}
.common_left_btn .circleProgress:hover {
  animation: circleProgressLoad_right 5s infinite linear;
  -webkit-animation: circleProgressLoad_right 5s infinite linear;
  -moz-animation: circleProgressLoad_right 5s infinite linear;
  -o-animation: circleProgressLoad_right 5s infinite linear;
}
.common_right_btn {
  margin-left: 0.066667rem;
  width: 0.653333rem;
  height: 0.653333rem;
  display: block;
  float: right;
  position: relative;
  cursor: pointer;
  border: 2px solid #bfbfbf;
  border-radius: 50%;
  background: url(/static/img/sprite/right.png) no-repeat center center;
}
.common_right_btn .circleProgress:hover {
  animation: circleProgressLoad_right 0.5s infinite linear;
  -webkit-animation: circleProgressLoad_right 5s infinite linear;
  -moz-animation: circleProgressLoad_right 5s infinite linear;
  -o-animation: circleProgressLoad_right 5s infinite linear;
}
.circleProgress {
  width: 0.653333rem;
  height: 0.653333rem;
  display: block;
  border: 0.026667rem solid transparent;
  border-radius: 50%;
  position: absolute;
  top: -0.026667rem;
  left: -0.026667rem;
  -webkit-transform: rotate(45deg);
}
@keyframes circleProgressLoad_right {
  0% {
    border-top: 2px solid #E84358;
    border-right: 2px solid #E84358;
    -webkit-transform: rotate(0deg);
  }
  100% {
    border-left: 2px solid #BFBFBF;
    border-bottom: 2px solid #BFBFBF;
    -webkit-transform: rotate(360deg);
  }
}
@-moz-keyframes circleProgressLoad_right {
  0% {
    border-top: 2px solid #E84358;
    border-right: 2px solid #E84358;
    -webkit-transform: rotate(0deg);
  }
  100% {
    border-left: 2px solid #BFBFBF;
    border-bottom: 2px solid #BFBFBF;
    -webkit-transform: rotate(360deg);
  }
}
@-webkit-keyframes circleProgressLoad_right {
  0% {
    border-top: 2px solid #E84358;
    border-right: 2px solid #E84358;
    -webkit-transform: rotate(0deg);
  }
  100% {
    border-left: 2px solid #BFBFBF;
    border-bottom: 2px solid #BFBFBF;
    -webkit-transform: rotate(360deg);
  }
}
@-o-keyframes circleProgressLoad_right {
  0% {
    border-top: 2px solid #E84358;
    border-right: 2px solid #E84358;
    -webkit-transform: rotate(0deg);
  }
  100% {
    border-left: 2px solid #BFBFBF;
    border-bottom: 2px solid #BFBFBF;
    -webkit-transform: rotate(360deg);
  }
}
@keyframes circleProgressLoad_left {
  0% {
