.m-load {
  z-index: 99999;
  width: 0.48rem;
  height: 0.48rem;
  margin: 1.333333rem auto;
  position: fixed!important;
  top: 40%;
  right: 50%;
}
/** 加载动画的静态样式 **/
.m-load {
  position: relative;
}
.m-load .line div {
  position: absolute;
  left: 0.213333rem;
  top: 0;
  width: 0.04rem;
  height: 0.48rem;
}
.m-load .line div:before,
.m-load .line div:after {
  content: '';
  display: block;
  height: 50%;
  background: #fcfcfc;
  border-radius: 0.066667rem;
}
.m-load .line div:nth-child(2) {
  -webkit-transform: rotate(30deg);
}
.m-load .line div:nth-child(3) {
  -webkit-transform: rotate(60deg);
}
.m-load .line div:nth-child(4) {
  -webkit-transform: rotate(90deg);
}
.m-load .line div:nth-child(5) {
  -webkit-transform: rotate(120deg);
}
.m-load .line div:nth-child(6) {
  -webkit-transform: rotate(150deg);
}
.m-load .circlebg {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 0.24rem;
  height: 0.24rem;
  margin: -0.12rem 0 0 -0.12rem;
  background: #40424b;
  border-radius: 0.24rem;
}
/** 加载动画 **/
@keyframes load {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@-o-keyframes load {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@-webkit-keyframes load {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@-moz-keyframes load {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.m-load .line div:nth-child(1):before {
  -webkit-animation: load 1.2s linear 0s infinite;
}
.m-load .line div:nth-child(2):before {
  -webkit-animation: load 1.2s linear 0.1s infinite;
}
.m-load .line div:nth-child(3):before {
  -webkit-animation: load 1.2s linear 0.2s infinite;
}
.m-load .line div:nth-child(4):before {
  -webkit-animation: load 1.2s linear 0.3s infinite;
}
.m-load .line div:nth-child(5):before {
  -webkit-animation: load 1.2s linear 0.4s infinite;
}
.m-load .line div:nth-child(6):before {
  -webkit-animation: load 1.2s linear 0.5s infinite;
}
.m-load .line div:nth-child(1):after {
  -webkit-animation: load 1.2s linear 0.6s infinite;
}
.m-load .line div:nth-child(2):after {
  -webkit-animation: load 1.2s linear 0.7s infinite;
}
.m-load .line div:nth-child(3):after {
  -webkit-animation: load 1.2s linear 0.8s infinite;
}
.m-load .line div:nth-child(4):after {
  -webkit-animation: load 1.2s linear 0.9s infinite;
}
.m-load .line div:nth-child(5):after {
  -webkit-animation: load 1.2s linear 1s infinite;
}
.m-load .line div:nth-child(6):after {
  -webkit-animation: load 1.2s linear 1.1s infinite;
}
