.content_landlord {
  height: 6.546667rem;
  background: url(/static/img/landlords_bg.png) -1.56rem -1.32rem;
}
.content_landlord .common_content_title {
  margin-top: 1.333333rem;
}
.content_landlord_img {
  width: 100%;
}
.content_landlord_img_item {
  padding-top: 0.133333rem;
  width: 3.333333rem;
  display: inline-block;
  text-align: center;
}
.content_landlord_img_item img {
  width: 0.653333rem;
  height: 0.666667rem;
}
.content_landlord_img_item_name {
  margin-top: 0.106667rem;
  font-size: 16px;
  font-family: Futura-Medium;
  font-weight: 500;
  color: #666666FF;
}
.content_comment {
  height: 7.026667rem;
  background: #f7f7f7;
  text-align: center;
}
.content_comment > div {
  background: url(/static/img/comma.png) no-repeat 1.693333rem 0.306667rem;
}
.content_comment .common_content_title {
  margin-top: 0.666667rem;
}
.content_comment .common_left_btn,
.content_comment .common_right_btn {
  top: 0.586667rem;
}
.content_comment_slide {
  margin-top: 0.706667rem;
}
.content_comment_tall_content {
  text-align: justify;
  width: 12.36rem;
  height: 1.933333rem;
  line-height: 24px;
  margin: 0 auto;
  color: #FFFFFF;
  background: -webkit-linear-gradient(#F2A48C, #EA5260);
  /* Safari 5.1 - 6.0 */
  background: -o-linear-gradient(#F2A48C, #EA5260);
  /* Opera 11.1 - 12.0 */
  background: -moz-linear-gradient(#F2A48C, #EA5260);
  /* Firefox 3.6 - 15 */
  background: linear-gradient(#F2A48C, #EA5260);
  /* 标准的语法 */
}
.content_comment_tall_content div {
  font-size: 16px;
  font-weight: 500;
  padding: 0.4rem 0.226667rem 0 0.226667rem;
  text-align: justify;
}
.content_comment_tall_thir {
  border-width: 0.266667rem 0.266667rem 0;
  border-style: solid;
  border-color: #eb525e transparent transparent;
  margin: 0 auto;
  width: 0;
}
.content_comment_tall_head {
  position: relative;
  overflow: hidden;
  margin: 0 auto;
  width: 1.146667rem;
  height: 1.146667rem;
  margin-top: 14px;
  border: 2px solid #E84358;
  border-radius: 50%;
}
.content_comment_tall_head img {
  width: 100%;
  position: relative;
}
.content_comment_tall_title {
  margin: 0 auto;
  top: 0.32rem;
  position: relative;
  font-size: 18px;
  font-family: Futura-Bold;
  font-weight: bold;
  color: #333333;
}
.content_comment_tall_text {
  margin: 0 auto;
  top: 0.346667rem;
  position: relative;
  font-size: 18px;
  font-family: Futura-Medium;
  font-weight: 500;
  color: #999999;
}
.content_care .common_content_title {
  margin-top: 1.32rem;
}
.content_care_img {
  vertical-align: top;
  display: inline-block;
  width: 8.733333rem;
  height: 5.72rem;
  margin-right: 0.733333rem;
  position: relative;
  margin-top: 0.266667rem;
}
.content_care_img:before {
  content: "";
  display: block;
  background: #e84358;
  width: 0.986667rem;
  height: 0.346667rem;
  position: absolute;
  right: -0.453333rem;
  top: 1.186667rem;
}
.content_care_box {
  margin-top: 1.04rem;
}
.content_care_list {
  display: inline-block;
  width: 5.466667rem;
  float: right;
  margin-bottom: 0.466667rem;
}
.content_care_list_item {
  text-align: left;
  line-height: 0.44rem;
  padding: 0.173333rem 0;
  display: block;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}
.content_care_list_item .title {
  font-size: 18px;
  font-family: Futura-Bold;
  font-weight: bold;
  color: #333333;
}
.content_care_list_item .content {
  font-size: 18px;
  font-family: Futura-Medium;
  font-weight: 500;
  color: #999999;
}
.content_care_list_item:hover {
  background: #fbfbfb;
  cursor: pointer;
}
